.calculator {
  max-width: 320px; /* Matches Casio portrait size */
  margin: 0 auto;
  padding: 0.5rem;
  border: 2px solid #333;
  border-radius: 8px;
  background: linear-gradient(to bottom, #f0f0f0, #d0d0d0);
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  font-family: 'Courier New', monospace; /* Casio-like font */
}

.top-keys {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 0.1rem;
  margin-bottom: 0.5rem;
}

.top-keys button {
  padding: 0.3rem 0.5rem;
  font-size: 0.7rem;
  background: #c0c0c0;
  border: 1px solid #999;
  border-radius: 3px;
}

.display {
  background: #000;
  color: #0f0; /* Green LCD glow */
  font-size: 1.2rem;
  font-family: 'Courier New', monospace;
  padding: 0.5rem;
  text-align: left;
  min-height: 1.5rem;
  border: 1px solid #333;
  border-radius: 4px;
  margin-bottom: 0.5rem;
  overflow: hidden;
  white-space: pre;
}

.key-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* 6 cols for Casio width */
  grid-template-rows: repeat(8, 1fr); /* 8 rows: F-keys, sci, numbers */
  gap: 0.1rem; /* Tight for authentic feel */
}

/* Position buttons by grid area - mimic FX-5800P */
.key:nth-child(1) { grid-row: 1; grid-column: 1; } /* F1 */
.key:nth-child(2) { grid-row: 1; grid-column: 2; } /* F2 */
.key:nth-child(3) { grid-row: 1; grid-column: 3; } /* F3 */
.key:nth-child(4) { grid-row: 1; grid-column: 4; } /* F4 */
.key:nth-child(5) { grid-row: 1; grid-column: 5; } /* F5 */
.key:nth-child(6) { grid-row: 1; grid-column: 6; } /* F6 */
.key:nth-child(7) { grid-row: 2; grid-column: 1; } /* sin */
.key:nth-child(8) { grid-row: 2; grid-column: 2; } /* cos */
.key:nth-child(9) { grid-row: 2; grid-column: 3; } /* tan */
.key:nth-child(10) { grid-row: 2; grid-column: 4; } /* 10^x */
.key:nth-child(11) { grid-row: 2; grid-column: 5; } /* log */
.key:nth-child(12) { grid-row: 2; grid-column: 6; } /* ln */
.key:nth-child(13) { grid-row: 3; grid-column: 1; } /* RCL */
.key:nth-child(14) { grid-row: 3; grid-column: 2; } /* ENG */
.key:nth-child(15) { grid-row: 3; grid-column: 4; } /* F1 (repeat for sci) */
.key:nth-child(16) { grid-row: 3; grid-column: 5; } /* F2 */
.key:nth-child(17) { grid-row: 3; grid-column: 6; } /* F3 */
/* Numbers tight in bottom-left (rows 4-7, cols 1-3) */
.key:nth-child(18) { grid-row: 4; grid-column: 1; } /* 7 */
.key:nth-child(19) { grid-row: 4; grid-column: 2; } /* 8 */
.key:nth-child(20) { grid-row: 4; grid-column: 3; } /* 9 */
.key:nth-child(21) { grid-row: 4; grid-column: 4; } /* DEL */
.key:nth-child(22) { grid-row: 4; grid-column: 5; } /* AC */
.key:nth-child(23) { grid-row: 5; grid-column: 1; } /* 4 */
.key:nth-child(24) { grid-row: 5; grid-column: 2; } /* 5 */
.key:nth-child(25) { grid-row: 5; grid-column: 3; } /* 6 */
.key:nth-child(26) { grid-row: 5; grid-column: 4; } /* × */
.key:nth-child(27) { grid-row: 5; grid-column: 5; } /* ENG */
.key:nth-child(28) { grid-row: 6; grid-column: 1; } /* 1 */
.key:nth-child(29) { grid-row: 6; grid-column: 2; } /* 2 */
.key:nth-child(30) { grid-row: 6; grid-column: 3; } /* 3 */
.key:nth-child(31) { grid-row: 6; grid-column: 4; } /* + */
.key:nth-child(32) { grid-row: 6; grid-column: 5; } /* - */
.key:nth-child(33) { grid-row: 7; grid-column: 1; } /* 0 */
.key:nth-child(34) { grid-row: 7; grid-column: 2; } /* . */
.key:nth-child(35) { grid-row: 7; grid-column: 3; } /* ×10^ */
.key:nth-child(36) { grid-row: 7; grid-column: 4; } /* = */
.key:nth-child(37) { grid-row: 7; grid-column: 5; } /* EXE */
.key:nth-child(38) { grid-row: 8; grid-column: 6; } /* GRAPH (bottom-right) */

.key {
  padding: 0.8rem 0.4rem; /* Compact for Casio feel */
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  border: 1px solid #666;
  border-radius: 2px;
  cursor: pointer;
  transition: background 0.1s;
  background: #c0c0c0;
}

.key:hover { background: #d0d0d0; }
.key:active { background: #a0a0a0; }

/* Grouped classes */
.num-key { background: #e0e0e0; grid-column: 1 / 4; } /* Numbers tight left */
.op-key { background: #b0b0b0; grid-column: 4 / 6; } /* Operators right */
.sci-key { background: #90c0f0; } /* Blue for scientific */
.f-key { background: #f0f090; } /* Yellow for F-keys */

.graph-section {
  margin-top: 0.5rem;
  height: 200px; /* Compact plot for embed */
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
}

.history {
  margin-top: 0.5rem;
  font-size: 0.7rem;
  max-height: 80px;
  overflow-y: auto;
}

.history h4 { margin: 0 0 0.2rem 0; font-size: 0.8rem; }
.history p { margin: 0.1rem 0; font-family: monospace; }

/* Responsive for portfolio iframe */
@media (max-width: 400px) {
  .calculator { max-width: 100%; padding: 0.3rem; }
  .key { padding: 0.6rem 0.3rem; font-size: 0.8rem; }
  .graph-section { height: 150px; }
}
